.GrossParent {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 50px 550px 550px 550px 200px 200px 200px;
    grid-template-areas: "N" "R" "W" "S" "F" "C" "A";
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 400% 400%;
    animation: gradient 5s ease infinite;
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

.Navigation {
    grid-area: N;
    color: yellow;
    text-align: center;
    grid-column: 1/13;
    grid-row: 1/2;
    border-bottom: 2px solid yellow;
}

.hidden-menu {
    display: block;
    position: fixed;
    list-style:none;
    margin: 0;
    box-sizing: border-box;
    width: 100%;
    background: rgba(0, 170, 238, 0.9); /* Цвет фона */
    color: #fff; /* Цвет текста */
    padding: 150px; /* Поля вокруг текста */
    font-weight: bold;
    font-size: 50px;
    text-decoration: none;
    height: 100%;
    top: 0;
    right: -100%;
    transition: right .2s;
    z-index: 2;
    transform: translateZ(0);
    backface-visibility: hidden;
}
.hidden-menu a {
    text-decoration: none;
}

.hidden-menu a:hover {
    color: yellow;
    background-color: orange;
}

.hidden-menu-ticker {
    display: none;
}

.btn-menu {
    color: #fff;
    background-color: red;
    padding: 5px;
    position: fixed;
    top: 5px;
    right: 5px;
    cursor: pointer;
    transition: right .23s;
    z-index: 3;
    width: 25px;
    transform: translateZ(0);
    backface-visibility: hidden;
}
.btn-menu span {
    display: block;
    height: 5px;
    background-color: yellow;
    margin: 5px 0 0;
    transition: all .1s linear .23s;
    position: relative;
}
.btn-menu span.first {
    margin-top: 0;
}

.hidden-menu-ticker:checked ~ .btn-menu {
    right: 95%;
}
.hidden-menu-ticker:checked ~ .hidden-menu {
    right: 0;
}
.hidden-menu-ticker:checked ~ .btn-menu span.first {
    transform: rotate(45deg);
    top: 10px;
}
.hidden-menu-ticker:checked ~ .btn-menu span.second {
    opacity: 0;
}
.hidden-menu-ticker:checked ~ .btn-menu span.third {
    transform: rotate(-45deg);
    top: -10px;
}

.Resume {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr;
    grid-template-areas: "represent represent show";
    grid-area: R;
    color: green;
    text-align: center;
    grid-column: 1/13;
    grid-row: 2/3;
    border-bottom: 2px solid yellow;
}

.t1 {
    font-size: 80px;
    color: red;
}

.t3 {
    font-size: 60px;
    color: yellow;
}

.represent {
    grid-area: represent;
    grid-column: 1/3;
    position: relative;
}
.text {
    color: orange;
    font: normal 40px Varela Round, sans-serif;
    height: 50%;
    letter-spacing: 5px;
    position: absolute;
    text-align: center;
    width: 70%;
    animation: move linear 2000ms infinite;
    transition: all 2s ease-in-out;
}

.Resume:hover .text {
    transform: translate(150px,150px);
}

@keyframes move {
    0% {
        text-shadow:
                4px -4px 0 hsla(0, 100%, 50%, 1),
                3px -3px 0 hsla(0, 100%, 50%, 1),
                2px -2px 0 hsla(0, 100%, 50%, 1),
                1px -1px 0 hsla(0, 100%, 50%, 1),
                -4px 4px 0 hsla(180, 100%, 50%, 1),
                -3px 3px 0 hsla(180, 100%, 50%, 1),
                -2px 2px 0 hsla(180, 100%, 50%, 1),
                -1px 1px 0 hsla(180, 100%, 50%, 1)
    ;
    }
    25% {
        text-shadow:
                -4px -4px 0 hsla(180, 100%, 50%, 1),
                -3px -3px 0 hsla(180, 100%, 50%, 1),
                -2px -2px 0 hsla(180, 100%, 50%, 1),
                -1px -1px 0 hsla(180, 100%, 50%, 1),
                4px 4px 0 hsla(0, 100%, 50%, 1),
                3px 3px 0 hsla(0, 100%, 50%, 1),
                2px 2px 0 hsla(0, 100%, 50%, 1),
                1px 1px 0 hsla(0, 100%, 50%, 1)
    ;
    }
    50% {
        text-shadow:
                -4px 4px 0 hsla(0, 100%, 50%, 1),
                -3px 3px 0 hsla(0, 100%, 50%, 1),
                -2px 2px 0 hsla(0, 100%, 50%, 1),
                -1px 1px 0 hsla(0, 100%, 50%, 1),
                4px -4px 0 hsla(180, 100%, 50%, 1),
                3px -3px 0 hsla(180, 100%, 50%, 1),
                2px -2px 0 hsla(180, 100%, 50%, 1),
                1px -1px 0 hsla(180, 100%, 50%, 1)
    ;
    }
    75% {
        text-shadow:
                4px 4px 0 hsla(180, 100%, 50%, 1),
                3px 3px 0 hsla(180, 100%, 50%, 1),
                2px 2px 0 hsla(180, 100%, 50%, 1),
                1px 1px 0 hsla(180, 100%, 50%, 1),
                -4px -4px 0 hsla(0, 100%, 50%, 1),
                -3px -3px 0 hsla(0, 100%, 50%, 1),
                -2px -2px 0 hsla(0, 100%, 50%, 1),
                -1px -1px 0 hsla(0, 100%, 50%, 1)
    ;
    }
    100% {
        text-shadow:
                4px -4px 0 hsla(0, 100%, 50%, 1),
                3px -3px 0 hsla(0, 100%, 50%, 1),
                2px -2px 0 hsla(0, 100%, 50%, 1),
                1px -1px 0 hsla(0, 100%, 50%, 1),
                -4px 4px 0 hsla(180, 100%, 50%, 1),
                -3px 3px 0 hsla(180, 100%, 50%, 1),
                -2px 2px 0 hsla(180, 100%, 50%, 1),
                -1px 1px 0 hsla(180, 100%, 50%, 1)
    ;
    }
}
.show {
    grid-area: show;
    grid-column: -1/-2;
}

.Resume:hover .flipper {
    -webkit-transform: rotateY(180deg);
    -moz-transform: rotateY(180deg);
    -o-transform: rotateY(180deg);
    transform: rotateY(180deg);
}

.flip-container, .front, .back {
    width: 320px;
    height: 500px;
    margin-top: 10px;
}

.flipper {
    -webkit-transition: 0.6s;
    -webkit-transform-style: preserve-3d;

    -moz-transition: 0.6s;
    -moz-transform-style: preserve-3d;

    -o-transition: 0.6s;
    -o-transform-style: preserve-3d;

    transition: 0.6s;
    transform-style: preserve-3d;

    position: relative;
}

.front, .back {
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -o-backface-visibility: hidden;
    backface-visibility: hidden;

    position: absolute;
}

.front {
    background: url("./react-logo.png") 0 0 no-repeat;
    z-index: 2;
}

.back {
    -webkit-transform: rotateY(180deg);
    -moz-transform: rotateY(180deg);
    -o-transform: rotateY(180deg);
    transform: rotateY(180deg);

    background: url("./logo1.jpg") 0 0 no-repeat;
}

.MyWorks {
    grid-area: W;
    text-align: center;
    grid-column: 1/13;
    grid-row: 3/4;
    border-bottom: 2px solid yellow;
}

.titleWorks {
    height: 60px;
    width: 400px;
    margin: auto;
    margin-top: 20px;
    border-radius: 50%;
    border: 2px solid yellow;
    color: yellow;
    font-size: 40px;
}

* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -o-box-sizing: border-box;
    box-sizing: border-box;
}
.wrap {
    height: 350px;
    margin: 50px auto 0;
    position: relative;
    width: 500px;
}
.wrap > input {
    display: none;
}
.slider {
    background-color: #333;
    height: inherit;
    overflow: hidden;
    position: relative;
    width: inherit;
}
.slides {
    height: inherit;
    opacity: 0;
    position: absolute;
    width: inherit;
    z-index: 0;
    transform: rotate(55deg);
    -webkit-filter: hue-rotate(90deg);
    transition: 1s;
}
.img1 { background-image: url(lion.jpg); }
.img2 { background-image: url(tiger.jpg); }
.img3 { background-image: url(pantera.jpg); }

.wrap .control {
    position: absolute;
    margin-left: -50px;
    left: 50%;
}
.wrap label {
    cursor: pointer;
    display: inline-block;
    height: 8px;
    margin: 10px;
    position: relative;
    width: 20px;
    border-radius: 20%;
}
.wrap label:after {
    border: 2px solid green;
    content: " ";
    display: block;
    height: 12px;
    left: -4px;
    position: absolute;
    top: -4px;
    width: 20px;
    border-radius: 20%;
}

#point1:checked ~ .control label:nth-of-type(1),
#point2:checked ~ .control label:nth-of-type(2),
#point3:checked ~ .control label:nth-of-type(3) {
    background: yellow;
}
#point1:checked ~ .slider > .img1,
#point2:checked ~ .slider > .img2,
#point3:checked ~ .slider > .img3 {
    opacity: 1;
    z-index: 1;
    transform: none;
    -webkit-filter: none;
}
